import React from 'react';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import Link from '@mui/material/Link';
import CardActionArea from '@mui/material/CardActionArea';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';

import PlaceholderBanner from "../assets/placeholder.png";
import AFCBanner from "../assets/thumbnails/afc.png";
import FitnessBanner from "../assets/thumbnails/fitness.png";
import ZOTAPOTTYBanner from "../assets/thumbnails/zotapotty.png";
import UniqloBanner from "../assets/thumbnails/uniqlo.png";

import SmallAFCBanner from "../assets/thumbnails/afc-sm.png";
import SmallFitnessBanner from "../assets/thumbnails/fitness-sm.png";
import SmallZOTAPOTTYBanner from "../assets/thumbnails/zotapotty-sm.png";
import SmallUniqloBanner from "../assets/thumbnails/uniqlo-sm.png";




const ProjectCard = () => {

    // *edit project data here*
    const projectData = [
        {name: "[Coming Soon] Abound Food Care", description: "Designing a volunteer management system", tags: ["Web design", "Usability Testing"], tagColor: "#E8F1F2", tagTextColor: "#73A1A9", image: AFCBanner, bgColor: "#B3D0D5", link: ""},
        // {name: "Fitness", description: "Creating a social fitness app", tags: ["UX/UI design", "Gamification"], tagColor: "#A5AB95", image: SmallFitnessBanner, bgColor: "#CDD1C3", link: "/"},
        {name: "ZOTAPOTTY", description: "Designing a UCI restroom finder and review app", tags: ["UX Design", "App Design"], tagColor: "#FCF6E7", tagTextColor: "#DBA72A", image: ZOTAPOTTYBanner, bgColor: "#F7E3B0", link: "zotapotty"},
        {name: "UX Case Study: Uniqlo", description: "Researching & redesigning the Uniqlo mobile app", tags: ["Redesign", "UX research"], tagColor: "#FCF3FA", tagTextColor: "#BC88AF", image: UniqloBanner, bgColor: "#DDC9D8", link: "uniqlo"},
    ]

    return (
            <Grid container>
                {projectData.map((project) => (
                    <Grid item direction="column" xs={12} align="center" sx={{py: 8}}>
                        <Card elevation={0} style={{borderRadius: 15}}>
                            <CardActionArea href={project.link} id="project-card">
                                <CardMedia
                                    component="img"
                                    image={project.image}
                                    style={{borderRadius: 15}}
                                />
                                </CardActionArea>
                                <CardContent elevation={0} align="left" style={{backgroundColor: "inherit"}}>
                                    <Grid container xs={12}>
                                        <Grid item sx={{ display: 'contents'}}>
                                            <h1 class="card">{project.name}</h1>
                                        {/* </Grid>
                                        <Grid item xs={4}> */}
                                            {project.tags.map((tag) => (
                                                <Button variant="contained" disabled style={{height: 36, backgroundColor: project.tagColor, borderRadius: 30, fontFamily: "Montserrat", fontWeight: 600, color: project.tagTextColor, margin: "1.2rem 0 0 1rem"}}>
                                                    {tag}
                                                </Button>
                                            ))}
                                        </Grid>
                                        <Grid item xs={12}>
                                            <h2 class="card">{project.description}</h2>
                                        </Grid>
                                    </Grid>
                                </CardContent>
                            </Card>
                    </Grid>
                ))}
            </Grid>
    );
}

export default ProjectCard;



