
import { render } from "react-dom";
import { HashRouter, Routes, Route } from "react-router-dom";
import "./index.css";
import App from "./app";
import AboutMe from "./pages/about-me";
import Zotapotty from "./pages/zotapotty";
import Uniqlo from "./pages/uniqlo";
import AboundFoodCare from "./pages/afc";



const rootElement = document.getElementById("root");
render(
  <HashRouter basename={process.env.PUBLIC_URL}>
    <Routes>
      <Route path="/" element={<App/>} />
      <Route path="aboutme" element={<AboutMe/>} />
      <Route path="uniqlo" element={<Uniqlo/>} />
      <Route path="zotapotty" element={<Zotapotty/>} />
      <Route path="aboundfoodcare" element={<AboundFoodCare/>} />
      <Route 
        path="*"
        element={
          <main style={{ padding: "1rem"}}>
            <p>There's nothing here!</p>
          </main>
        } 
        />
    </Routes>
  </HashRouter>,
  rootElement
);